import React, { useState, useEffect } from "react";
import { Modal, Form, Divider, Button, message } from "antd";
import { FormInput, FlexEnd } from "..";
import { usePostData } from "../../Hooks";
import { setInput } from "../../Utils/myFormat";
import { FiNavigation } from "react-icons/fi";

const ModalCreateReligion = ({ visible, onCancel, setUpdateData }) => {
  const [religion, setReligion] = useState("");

  const resetState = () => {
    setReligion("");
    form.resetFields();
  };

  const [form] = Form.useForm();

  const [dataPost, setDataPost] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess, loading } = usePostData(
    dataPost.url,
    dataPost.data,
    dataPost.trigger
  );

  const handlePostReligion = () => {
    if (!religion) {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    const dataPost = {
      name: religion,
    };
    console.log("data post: ", dataPost);

    setDataPost({
      url: "employee/religion/add",
      data: dataPost,
      trigger: new Date().getTime(),
    });
  };

  useEffect(() => {
    if (onSuccess.includes("success")) {
      message.success(`Berhasil Create Religion ${religion}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onSuccess]);

  return (
    <Modal
      title="Create Religion"
      visible={visible}
      onCancel={onCancel}
      footer={false}
    >
      <Form form={form} name="createsite" autoComplete="off">
        <FormInput
          label="Religion"
          requiredLabel
          name="religion"
          required
          message="please input religion!"
          placeholder="input religion"
          onChange={(e) => setInput(e, setReligion)}
        />
        <Divider />
        <FlexEnd>
          <Form.Item>
            <Button
              type="primary"
              shape="round"
              icon={<FiNavigation style={{ marginRight: "6px" }} />}
              onClick={handlePostReligion}
              loading={loading}
              htmlType="submit"
            >
              Create Religion
            </Button>
          </Form.Item>
        </FlexEnd>
      </Form>
    </Modal>
  );
};

export default ModalCreateReligion;
