import React, { useState, useRef, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, Typography, Row, Button, Form, Table } from "antd";
import withAuth from "../../HOC/withAuth";
import { Card, FormInput, GridLarge, GridSmall } from "../../Components";
import { FiPlusSquare, FiSearch } from "react-icons/fi";
import { inActiveColor } from "../../Utils/colors";
import { useGetData, useDisclosure } from "../../Hooks";
import debounce from "lodash.debounce";
import ModalCreateReligion from "../../Components/UserManagement/ModalCreateReligion";

const { Title } = Typography;

const Religion = () => {
  const [updateData, setUpdateData] = useState(0);
  const { data, loading } = useGetData("employee/religion/list", updateData);

  const [searchData, setSearchData] = useState("");

  const newReligion = useMemo(
    () =>
      data.length === 0
        ? []
        : data.filter((item) =>
            item.name?.toLowerCase().includes(searchData?.toLowerCase())
          ),
    [data, searchData]
  );

  const { isOpen, onOpen, onClose } = useDisclosure();
  const navigate = useNavigate();

  const handleSearchData = useRef(
    debounce((valueSearch) => setSearchData(valueSearch), 1000)
  ).current;

  const columns = [
    {
      title: "#",
      dataIndex: "id",
      width: 70,
    },
    {
      title: "Religion Name",
      dataIndex: "name",
    },
  ];

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item
          style={{ cursor: "pointer" }}
          onClick={() => navigate("/user-management")}
        >
          User Management
        </Breadcrumb.Item>
        <Breadcrumb.Item>Religion</Breadcrumb.Item>
      </Breadcrumb>
      <ModalCreateReligion
        visible={isOpen}
        onCancel={onClose}
        setUpdateData={setUpdateData}
      />
      <div>
        <Card>
          <Title
            level={3}
            type="secondary"
            style={{ marginRight: 20, marginBottom: 30 }}
          >
            Religion
          </Title>
          <Row gutter={[16, 10]}>
            <GridLarge>
              <Button
                shape="round"
                icon={<FiPlusSquare style={{ marginRight: 6 }} />}
                onClick={onOpen}
              >
                New Religion
              </Button>
            </GridLarge>
            <GridSmall>
              <Form name="searchReligion" autoComplete="off">
                <FormInput
                  name="search"
                  prefix={<FiSearch color={inActiveColor} />}
                  allowClear={true}
                  placeholder="serach religion"
                  value={searchData}
                  onChange={(val) => handleSearchData(val.target.value)}
                />
              </Form>
            </GridSmall>
          </Row>
          <div style={{ marginTop: "20px" }}>
            <Table
              columns={columns}
              dataSource={newReligion}
              rowKey={(record) => record.id}
              loading={loading}
              scroll={{ y: 500 }}
            />
          </div>
        </Card>
      </div>
    </div>
  );
};

export default withAuth(Religion);
